import classes from './MenuToggle.module.css'
import { motion } from "framer-motion";


export default function MenuToggle(props) {
    const first_anim = {
        open: { rotate: -45, y: 8 },
        closed: { rotate: 0 },
    }
    const second_anim = {
        open: { opacity: 0 },
        closed: { opacity: 1 },
    }
    const third_anim = {
        open: { rotate: 45, y: -8 },
        closed: { rotate: 0 },
    }

    return (
        <button onClick={props.onClick} className={classes.button}>
            <div className={classes.wrapper}>
                <motion.div
                    className={classes.el}
                    animate={props.toggle ? "open" : "closed"}
                    variants={first_anim}
                />
                <motion.div
                    className={classes.el}
                    animate={props.toggle ? "open" : "closed"}
                    variants={second_anim}
                    transition={{ duration: 0.1 }}
                />
                <motion.div
                    className={classes.el}
                    animate={props.toggle ? "open" : "closed"}
                    variants={third_anim}
                />
            </div>
        </button>
    )
}