import classes from './Cards.module.css'
import { motion } from 'framer-motion'
import { useState } from 'react'
import {useSnackbar} from "notistack";


export default function Card(props) {
    const [isHovered, setIsHovered] = useState(false)

    const { enqueueSnackbar } = useSnackbar();
    const handleClickVariant = (variant) => () => {
        enqueueSnackbar('Ссылка не работает, это демо версия сайта', { variant });
    };

    return (
        <motion.div
            className={classes.wrapper}
            onMouseEnter={() => setIsHovered(true)}
            onMouseLeave={() => setIsHovered(false)}
            onTap={() => setIsHovered(true)}
            onTapCancel={() => setIsHovered(false)}
            animate={isHovered ? {scale: 1.05} : "null"}
            onClick={handleClickVariant('default')}
        >
            <div className={classes.image}>
                <div
                    style={{
                        backgroundColor: `${props.color}`, boxShadow: `0 0 50px ${props.color}`
                    }} className={classes.image_inner}>
                    {props.children}
                </div>
            </div>
            <div className={classes.title}>
                <h5>{props.header}</h5>
                <h4>{props.title}</h4>
            </div>
        </motion.div>
    )
}