import NavigationBar from "./layout/NavigationBar/NavigationBar";
import Header from "./layout/Header/Header";
import ControlSection from "./layout/ControlSection/ControlSection";
import SectionWithCard from "./layout/SectionWithCard/SectionWithCard";
import SectionWithNews from './layout/SectionWithNews/SectionWithNews'
import SectionWithAudience from "./layout/SectionWithAudence/SectionWithAudence";
import { ThemeProvider, createTheme } from '@mui/material/styles';
import SectionWithExamples from "./layout/SectionWithExamples/SectionWithExamples";
import Footer from './layout/Footer/Footer'
import { SnackbarProvider } from 'notistack';



function App() {
    let theme = createTheme({
        palette: {
            primary: {
                main: '#F72585',
            },
            secondary: {
                main: '#F72585',
            },
        },
    });
    theme = createTheme(theme, {
        palette: {
            info: {
                main: theme.palette.secondary.main,
            },
        },
    });


  return (
    <>
        <SnackbarProvider maxSnack={3}>
            <ThemeProvider theme={theme}>
                <div style={{overflow: 'hidden'}}>
                    <NavigationBar/>
                    <Header/>
                    <ControlSection/>
                    <SectionWithCard/>
                    <SectionWithNews/>
                    <SectionWithAudience/>
                    <SectionWithExamples/>
                    <Footer/>
                </div>
            </ThemeProvider>
        </SnackbarProvider>
    </>
  );
}

export default App;
