import Container from "@mui/material/Container";
import {TitleSection} from "../../UI/TitleSection/TitleSection";
import { useEffect, useState } from 'react'
import cardAnim from '../../../src/lotties/cardAnim.json'
import lottie from 'lottie-web'
import classes from './SectionWithCard.module.css'
import ButtonRed from '../../UI/Button/ButtonRed'
import LinkArrow from "../../UI/LinkArrow/LinkArrow";


export default function SectionWithCard() {
    const [cardAnimIsLoad, setCardAnimIsLoad] = useState(false)

    // Loading lottie animation
    useEffect(() => {
        if (!cardAnimIsLoad) {
            lottie.loadAnimation({
                container: document.querySelector("#card-anim"),
                animationData: cardAnim,
            })
            setCardAnimIsLoad(true)
        }
    })

    return (
        <Container className={classes.container} maxWidth="lg">
            <div id="card" className={classes.wrapper}>
                <div className={classes.card_anim}>
                    <div id="card-anim"/>
                </div>
                <div>
                    <TitleSection
                        sx={{maxWidth: '70%'}}
                        upper="Для разработчиков"
                        upper_wrapper_width="141"
                        dopTitle="Узнайте, как вы можете создавать финансовые функции за считанные минуты с помощью мощного API Unit, Dashboard, SDK и пользовательских интерфейсов."
                    >
                        <h1 className={classes.title_Selection}>Сделано для <span className={classes.title_Selection} style={{color: 'var(--lightBlue)'}}>разработчиков</span></h1>
                    </TitleSection>
                    <div className={classes.button}>
                        <ButtonRed classname={classes.button} text="Смотреть документы"/>
                    </div>
                    <div className={classes.link}>
                        <LinkArrow text="Подписаться на рассылку"/>
                    </div>
                </div>
            </div>
        </Container>
    )
}