import Container from "@mui/material/Container";
import classes from './SectionWithExamples.module.css'
import Cards from './Cards'
import ArrowForwardIosRoundedIcon from '@mui/icons-material/ArrowForwardIosRounded';
import { motion } from 'framer-motion'
import { useState, useRef, useEffect } from 'react'
import { useSwipeable } from 'react-swipeable'
import {useContainerDimensions} from "../../hooks/useContainerDimentions";


export default function SectionWithExamples() {
    const [scroll, setScroll] = useState(-3)
    const [leftScrollBound, setLeftScrollBounds] = useState(4)
    const [rightScrollBound, setRightScrollBounds] = useState(-7)
    console.log(scroll)

    const window = useRef()
    const {width} = useContainerDimensions(window)

    useEffect(() => {
        if (width >= 1900) {
            setLeftScrollBounds(1)
            setRightScrollBounds(-3)
            setScroll(-3)
        }
        if (width < 1900 && width > 1650) {
            setLeftScrollBounds(2)
            setRightScrollBounds(-3)
            setScroll(-2)
        }
        if (width <= 1650 && width >= 1500) {
            setLeftScrollBounds(2)
            setRightScrollBounds(-3)
            setScroll(-2)
        }
        if (width < 1500 && width >= 1300) {
            setLeftScrollBounds(2)
            setRightScrollBounds(-2)
            setScroll(-2)
        }
        if (width < 1300 && width >= 1000) {
            setLeftScrollBounds(2)
            setRightScrollBounds(-1)
            setScroll(0)
        }
        if (width < 100 && width >= 500) {
            setLeftScrollBounds(2)
            setRightScrollBounds(-1)
            setScroll(0)
        }
        if (width < 500) {
            setRightScrollBounds(0)
            setLeftScrollBounds(3)
            setScroll(0)
        }
    }, [width])

    const scrollRight = () => {
        if (scroll <= rightScrollBound ) {
            setScroll(rightScrollBound)
            return scroll
        } else {
            setScroll(scroll - 1)
            return scroll
        }
    }
    const scrollLeft = () => {
        if (scroll >= leftScrollBound ) {
            setScroll(leftScrollBound)
            return scroll
        } else {
            setScroll(scroll + 1)
            return scroll
        }
    }
    const handlerSwipe = useSwipeable({
        onSwiped: (eventData) => {
            if ( eventData.dir === 'Right') {
                console.log(scrollRight())
            } else {
                console.log(scrollLeft())
            }
        }
    })

    return (
        <div id="examples" ref={window} className={classes.ultra_wrapper} style={{width: '100%', overflow: 'hidden'}}>
            <Container maxWidth="lg">
                <div className={classes.wrapper}>
                    <div className={classes.title}>
                        <h1 className={classes.title_Selection}>Попробуйте с <span className={classes.title_Selection} style={{color: 'var(--magenta)'}}>этими проектами</span></h1>
                        <h4 style={{color: 'rgba(255, 255, 255, 0.8)', marginTop: '20px'}}>Выберите интересующий вас проект, и он перенаправит вас в наше приложение</h4>
                        <div className={classes.buttons}>
                            <motion.button
                                className={classes.button}
                                transition={{duration: 0.1}}
                                whileTap={{scale: 0.97}}
                                onClick={() => scrollLeft()}
                            >
                                <ArrowForwardIosRoundedIcon sx={{transform: 'rotate(180deg)', color: 'var(--white)'}}/>
                            </motion.button>
                            <motion.button
                                className={classes.button}
                                transition={{duration: 0.05}}
                                whileTap={{scale: 0.97}}
                                onClick={() => scrollRight()}
                            >
                                <ArrowForwardIosRoundedIcon sx={{color: 'var(--white)'}}/>
                            </motion.button>
                        </div>
                    </div>
                </div>
            </Container>
            <div {...handlerSwipe} className={classes.cards}>
                <motion.div
                    animate={{
                        x: -scroll * 420,
                        transition: {type: "spring", bounce: 0.3, duration: 0.4}
                    }}
                >
                    <Cards/>
                </motion.div>
            </div>
        </div>
    )
}