import Container from '@mui/material/Container';
import classes from './NavigationBar.module.css'
import logo from '../../graphic/Logo.svg'
import EastIcon from '@mui/icons-material/East';
import { useState } from 'react'
import { motion } from 'framer-motion'
import MenuToggle from "./MenuToggle"
import {useSnackbar} from "notistack";


export default function() {
    const [navIsOpen, setNavIsOpen] = useState(false)

    const navBarDevisesAnim = {
        open: {
            opacity: 1,
            y: -5,
            scaleY: 1,
            transition: { type: "spring", stiffness: 200, damping: 24 },
        },
        closed: {
            y: -150,
            opacity: 0,
            scaleY: 0,
            transition: { type: "spring", stiffness: 200, damping: 24 },
        }
    }
    const navElementsAnim = {
        open: custom => ({
            y: 0,
            opacity: 1,
            transition: { delay: custom * 0.05 + 0.12, type: 'spring' }
        }),
        closed: {y: 20, opacity: 0, transition: 0}
    }
    const { enqueueSnackbar } = useSnackbar();
    const handleClickVariant = (variant) => () => {
        enqueueSnackbar('Ссылка не работает, это демо версия сайта', { variant });
    };

    const scrollIt = (route) => {
        const to = document.getElementById(`${route}`);
        if (to) {
          window.scrollTo({
            top: to.offsetTop - 150,
            behavior: 'smooth',
          });
        }
    };

    const scrollMobHandler = (route) => {
        setNavIsOpen(false)
        scrollIt(route)
    }

    return (
        <div className={classes.navBar__wrapper}>
            <Container maxWidth="lg">
                <div className={classes.wrapper}>
                    <div className={classes.navigation}>
                        <img onClick={() => scrollIt('header')} className={classes.logo} src={logo} alt="logo"/>
                        <div className={classes.navigation__inner}>
                            <ul>
                                <li><button onClick={() => scrollIt('control')} style={{background: 'transparent', border: '0px solid transparent', padding: '0 0'}}><h4>Контроль</h4></button></li>
                                <li><button onClick={() => scrollIt('card')} style={{background: 'transparent', border: '0px solid transparent', padding: '0 0'}}><h4>Разработчикам</h4></button></li>
                                <li><button onClick={() => scrollIt('news')} style={{background: 'transparent', border: '0px solid transparent', padding: '0 0'}}><h4>Новости</h4></button></li>
                                <li><button onClick={() => scrollIt('audence')} style={{background: 'transparent', border: '0px solid transparent', padding: '0 0'}}><h4>Аудитория</h4></button></li>
                                <li><button onClick={() => scrollIt('examples')} style={{background: 'transparent', border: '0px solid transparent', padding: '0 0'}}><h4>Проекты</h4></button></li>
                            </ul>
                        </div>
                        <div className={classes.menu__icon}>
                            <MenuToggle toggle={navIsOpen} onClick={() => setNavIsOpen(!navIsOpen)}/>
                        </div>
                    </div>
                    <div className={classes.to__app}>
                        <h4 onClick={handleClickVariant('default')}>К приложению</h4>
                        <EastIcon fontSize='small'/>
                    </div>
                </div>
            </Container>
            <div onClick={() => setNavIsOpen(false)} className={navIsOpen ? classes.dev__nav__wrapper : classes.dev__nav__wrapper_closed}>
                <motion.div
                    className={classes.devises__navigation}
                    animate={navIsOpen ? "open" : "closed"}
                    variants={navBarDevisesAnim}
                    style={{originY: 0.55}}
                >
                    <ul>
                        <motion.li
                            onClick={() => scrollMobHandler('control')}
                            className={classes.navDevises}
                            custom={1}
                            variants={navElementsAnim}
                            animate={navIsOpen ? "open" : "closed"}
                        >
                            <h4>Контроль</h4>
                        </motion.li>
                        <motion.li
                            onClick={() => scrollMobHandler('card')}
                            className={classes.navDevises}
                            custom={2}
                            variants={navElementsAnim}
                            animate={navIsOpen ? "open" : "closed"}
                        >
                            <h4>Разработчикам</h4>
                        </motion.li>
                        <motion.li
                            onClick={() => scrollMobHandler('news')}
                            className={classes.navDevises}
                            custom={3}
                            variants={navElementsAnim}
                            animate={navIsOpen ? "open" : "closed"}
                        >
                            <h4>Новости</h4>
                        </motion.li>
                        <motion.li
                            onClick={() => scrollMobHandler('audence')}
                            className={classes.navDevises}
                            custom={4}
                            variants={navElementsAnim}
                            animate={navIsOpen ? "open" : "closed"}
                        >
                            <h4>Аудитория</h4>
                        </motion.li>
                        <motion.li
                            onClick={() => scrollMobHandler('examples')}
                            className={classes.navDevises}
                            custom={5}
                            variants={navElementsAnim}
                            animate={navIsOpen ? "open" : "closed"}
                        >
                            <h4>Проекты</h4>
                        </motion.li>
                    </ul>
                </motion.div>
            </div>
        </div>
    )
}