import classes from './SecondSectionImages.module.css'
import { motion } from "framer-motion";
import firsImg from '../../graphic/Frame 1.png'
import secondImg from '../../graphic/Frame 2.png'
import thirdImg from '../../graphic/Frame 3.png'
import FitbitRoundedIcon from '@mui/icons-material/FitbitRounded';


export const SecondSectionImages = () => {
    const first_img_anim = {
        open: {},
        closed: {},
    }
    const second_img_anim = {
        open: {x: 120, y: -100, scale: 0.4, opacity: 0},
        closed: {x: 90, y: -60, scale: 1, opacity: 1},
    }
    const third_img_anim = {
        open: {x: 160, y: -360, scale: 0.4, opacity: 0},
        closed: {x: 260, y: -300, scale: 1, opacity: 1},
    }
    const forth_img_anim = {
        open: {x: 200, y: -750, scale: 0.4, opacity: 0},
        closed: {x: 550, y: -750, scale: 1, opacity: 1},
    }

    return (
        <div className={classes.outer__wrapper}>
            <div className={classes.wrapper}>
                <motion.div
                    className={classes.first_img}
                >
                    <img src={firsImg} alt={""}/>
                </motion.div>
                <motion.div
                    className={classes.second_img}
                    initial="open"
                    whileInView="closed"
                    variants={second_img_anim}
                    transition={{delay: 0.2, duration: 1}}
                    viewport={{once: true}}
                >
                    <img src={secondImg} alt=""/>
                </motion.div>
                <motion.div
                    className={classes.third_img}
                    initial="open"
                    whileInView="closed"
                    variants={third_img_anim}
                    transition={{delay: 0.5, duration: 1}}
                    viewport={{once: true}}
                >
                    <img src={thirdImg} alt=""/>
                </motion.div>
                <motion.div
                    className={classes.forth_img}
                    initial="open"
                    whileInView="closed"
                    variants={forth_img_anim}
                    transition={{delay: 0.4, duration: 1}}
                    viewport={{once: true}}
                >
                    <FitbitRoundedIcon style={{width: '40px', height: '40px'}}/>
                </motion.div>
            </div>
        </div>
    )
}