import {motion} from 'framer-motion'
import classes from './LinkArrow.module.css'
import EastIcon from '@mui/icons-material/East';
import { useState, useEffect } from 'react'
import {useSnackbar} from "notistack";
import { useWillChange } from 'framer-motion'


export default function LinkArrow(props) {
    const [isHovered, setIsHovered] = useState(false)

    const arrowMotion = {
        initial: {
            x: 0,
            y: 0,
            opacity: [0, 1],
            transition: { type: "spring", stiffness: 1000, damping: 10000, duration: 0.01 }
        },
        motion: {
            x: 50,
            y: 0,
            opacity: 1,
            transition: { type: "spring", stiffness: 300, damping: 24, duration: 0.5 }
        }
    }

    const { enqueueSnackbar } = useSnackbar();
    const handleClickVariant = (variant) => () => {
        enqueueSnackbar('Ссылка не работает, это демо версия сайта', { variant });
    };

    const willChange = useWillChange()

    return (
        <motion.div
            onMouseEnter={() => setIsHovered(true)}
            onMouseLeave={() => setIsHovered(false)}
            onTapStart={() => setIsHovered(true)}
            onTapCancel={() => setIsHovered(false)}
            onTap={() => setIsHovered(false)}
            className={classes.wrapper}
            whileTap={{scale: 0.97}}
            transition={{duration: 0.1}}
            onClick={handleClickVariant('default')}
            style={{ willChange }}
        >
            <div className={classes.circle__wrapper}>
                <motion.div
                    variants={arrowMotion}
                    animate={isHovered ? "motion" : "initial"}
                    initial="initial"
                    className={classes.arrows}
                >
                    <EastIcon fontSize='small'/>
                    <EastIcon fontSize='small'/>
                </motion.div>
            </div>
            <motion.h5
                className={classes.text}
                animate={isHovered ? {scale: 0.97} : {scale: 1}}
            >
                {props.text}
            </motion.h5>
        </motion.div>
    )
}